const data = {
  labels: ["Red", "Blue", "Yellow"],
  datasets: [
    {
      label: "My First Dataset",
      data: [300, 50, 100],
      backgroundColor: [
        "rgb(255, 99, 132)",
        "rgb(54, 162, 235)",
        "rgb(255, 205, 86)",
      ],
      hoverOffset: 4,
    },
  ],
};

const projectProgress_data = {
  labels: ["in progress", "completed", "not started"],
  datasets: [
    {
      label: "Percent",
      data: [64, 25, 11],
      backgroundColor: [
        "rgb(255, 99, 132)",
        "rgb(54, 162, 235)",
        "rgb(255, 205, 86)",
      ],
      hoverOffset: 30,
    },
  ],
};
const project_progress = new Chart(
  document.querySelector("#project-progress canvas").getContext("2d"),
  {
    type: "doughnut",
    data: projectProgress_data,
  }
);
const steel = new Chart(
  document.querySelector("#steel canvas").getContext("2d"),
  {
    type: "pie",
    data: data,
  }
);
const cement = new Chart(
  document.querySelector("#cement canvas").getContext("2d"),
  {
    type: "pie",
    data: data,
  }
);
const cost_flow = new Chart(
  document.querySelector("#cost-flow canvas").getContext("2d"),
  {
    type: "bar",
    data: data,
  }
);
const time_table = new Chart(
  document.querySelector("#time-table canvas").getContext("2d"),
  {
    type: "bar",
    data: data,
  }
);

function test() {
  try {
    var Excel = new ActiveXObject("Excel.Application");
    Excel.Visible = true;
    Excel.Workbooks.Open("C:\\path_to_your_file\\Book1.xlsx"); // Use the full path to the Excel file
  } catch (error) {
    console.error("Error: " + error.message);
  }
}

let excelBtn = document.querySelector("#excelBtn"); // Fixed typo here
excelBtn.addEventListener("click", test);
